@import url(https://fonts.googleapis.com/css2?family=Source+Serif+Pro:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Maven+Pro:wght@500&display=swap);
#root {
    width: 100vw;
    height: 100vh;
    background: #004445;
}

.container {
    max-width: none !important;
    max-height: none !important;
    margin: 0 !important;
    padding: 0 !important;
}

.card-background {
    background: #004445;
}
/* Title Page */
.title-font {
    font-family: 'Source Serif Pro', serif;
    font-size: 50px;
}

.title-btn {
    display: inline-block;
    margin: 0;
    font-size: 18px;
    text-decoration: none;
    padding: 10px 40px;
    position: relative;
    overflow: hidden;
    border-radius: 15px;
}

.signup-btn {
    background-color: #fff1c1;
    color: #004445;
}

.login-btn {
    background-color: #004445;
    color: #fff1c1;
    border: 1px solid #fff1c1;
}

.title-btn::before {
    content: '';
    display: block;
    position: absolute;
}

.signup-btn::before {
    background-color: #fcea90;
}

.login-btn::before {
    background-color: #118a7e;
}

.title-btn span {
    font-family: 'Maven Pro', sans-serif;
    position: relative;
    z-index: 1;
}

.signup-btn, .login-btn {
    transition: all 0.5s;
}

.signup-btn::before, .login-btn::before {
    top: 50%;
    left: 50%;
    width: 100%;
    height: 0;
    transform: translate3d(-50%, -50%, 0) rotate(20deg);
    transition: all 0.5s;
}

.signup-btn:hover {
    border: 1px solid #fcea90;
}

.login-btn:hover {
    color: #fff1c1;
    border: 1px solid #004445;
}

.signup-btn:hover::before, .login-btn:hover::before {
    height: 400%;
}

.home-container {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: flex-start;
}

.float-left {
    width: 20%;
    position: relative;
}

.float-right {
    width: 80%;
    background: #fafafa;
    text-align: center;
    justify-content: center;
}

.title-container {
    margin-top: 35px;
}

.logout-container {
    position: absolute;
    bottom: 5%;
    width: 100%;
    text-align: center;
}

.signout-btn {
    font-size: 20px !important;
    color: #fff1c1 !important;
}

.chat-card {
    height: 100%;
} 

.select-all-btn {
    height: 30px;
    padding: 2px 15px 4px 15px !important;
    font-size: 16px !important;
    background: #004445 !important;
    border: none !important;
}

/* User Table */
.user-img-container {
    margin: 10px;
}

.username-container {
    margin: 15px;
}

.holder{
    background: #fff;
    border-radius:5px;  
    box-shadow: 0 2px 3px 0 rgba(0,0,0,.1); 
    width:400px;
}

.table-title, .table-select {
    padding-left: 30px;
    width: 50%;
}

.user-list-table {
    height: 200px;
    overflow: scroll;
}

tr {
    text-align: left;
}
  
td:nth-child(1) {
    width: 40px;
}

td:nth-child(2) {
    padding: 0 50px;
    min-width: 90px;
}

td:nth-child(3) {
    text-align: center;
    padding-left: 50px;
    position: relative;
}

.user-icon {
    height: 30px;
    width: 30px;
    background: #ececec;
    border-radius: 50%;
    text-align: left;
    margin: 10px;
}

/* Checkbox */

input[type=checkbox] {
    cursor: pointer;
    height: 30px;
    margin:4px 0 0;
    position: absolute;
    opacity: 0;
    width: 30px;
    z-index: 2;
}
  
input[type=checkbox] + span {
    background: #9ba6a5;
    border-radius: 50%;
    box-shadow: 0 2px 3px 0 rgba(0,0,0,.1);
    display: inline-block;
    height: 30px;
    margin:4px 0 0;
    position:relative;
    width: 30px;
    transition: all .2s ease;
}
  
input[type=checkbox] + span::before, input[type=checkbox] + span::after{
    background:#fff;
    content:'';
    display:block;
    position:absolute;
    width:4px;
    transition: all .2s ease;
}
  
input[type=checkbox] + span::before{
    height:16px;
    left:13px;
    top:7px;
    transform:rotate(-45deg);
}
  
input[type=checkbox] + span::after{
    height:16px;
    right:13px;
    top:7px;
    transform:rotate(45deg);
}
  
input[type=checkbox]:checked + span {
    background:#2ecc71;         
}
  
input[type=checkbox]:checked + span::before{
    height: 9px;
    left: 9px;
    top: 13px;
    transform:rotate(-47deg);
}
  
input[type=checkbox]:checked + span::after{
    height: 15px;
    right: 11px;
    top: 8px;
}

/* Message Box */
.message-container {
    width: 100%;
    height: 200px;
}
  
input[type=submit] {
    background-color: #004445;
    border: 0;
    border-radius: 4px;
    color: #FFF;
    cursor: pointer;
    display: inline-block;
    font-size:16px;
    text-align: center;
    padding: 12px 20px 14px;
    width: 100%;
    margin-top: 15px;
}
.continue-btn {
    color: #004445 !important;
    border: none !important;
    background: #fff1c1 !important;
    height: 45px;
}

.continue-btn:hover {
    background: #fde994 !important;
}

.option-text {
    color: #fff1c1;
}
